import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';
import afterSaleBox from './child/afterSaleBox'
import expressBox from './child/expressBox';
import empty from "@/components/empty";
import { mapState, mapActions } from 'vuex'
export default {
	components: {
		uniLoadMore,
		empty,
		afterSaleBox,
		expressBox
	},
	data() {
		return {
			tabCurrentIndex: 0,
			navList: [{
					state: 0,
					text: '全部',
					loadingType: 'more',
					orderList: [],
					reload: true,
					page: 1
				},
				{
					state: 1,
					text: '待付款',
					loadingType: 'more',
					orderList: [],
					reload: true,
					page: 1
				},
				{
					state: 2,
					text: '待收货',
					loadingType: 'more',
					orderList: [],
					reload: true,
					page: 1
				},
				{
					state: 3,
					text: '待评价',
					loadingType: 'more',
					orderList: [],
					reload: true,
					page: 1
				},
				/* {
					state: 4,
					text: '售后',
					loadingType: 'more',
					orderList: [],
					reload: true,
					page: 1
				} */
			],
			swiperHeight: '',
			refund_order_sn: '',
			currentOrder: null
		};
	},
	computed: {
		...mapState({
			app_info: state => state.app_info
		})
	},
	onLoad(options){
		/**
		 * 修复app端点击除全部订单外的按钮进入时不加载数据的问题
		 */
		this.tabCurrentIndex = +options.state;







		if(options.state == 0){
			this.loadData()
		}

		let info = uni.getSystemInfoSync()
		this.swiperHeight = info.windowHeight - 44 + 'px'
	},
	 
	methods: {
		//获取订单列表
		loadData(source) {
			//这里是将订单挂载到tab列表下
			let index = this.tabCurrentIndex;
			let navItem = this.navList[index];
			let state = navItem.state;
			if(source === 'tabChange' && navItem.loaded === true && navItem.reload == false){
				//tab切换只有第一次需要加载数据
				return;
			}
			if(navItem.loadingType === 'loading'){
				//防止重复加载
				return;
			}
			if (navItem.reload == true) navItem.orderList = []
			
			navItem.loadingType = 'loading';
			this.$http.get('order.list', {state: navItem.state, page: navItem.page}).then(res => {
				let orderList = res.data.data
				// this.clacIsReview(orderList)
				orderList.forEach(item => {
					navItem.orderList.push(item)
				})
				navItem.loaded = true
				navItem.reload = false
				this.$set(navItem, 'page', navItem.page + 1)
				if (res.data.current_page == res.data.last_page) navItem.loadingType = 'nomore'
				else navItem.loadingType = 'more'
			}).catch(e => { console.log(e) })
		}, 
		/* clacIsReview(list) {
			list.forEach((item, index) => {
				let res = 1
				for(let i = 0; i < item.products.length; i ++) {
					if (item.products[i].buyer_review == 0) {
						res = 0
						break;
					}
				}
				list[index].buyer_review = res
			})
		}, */
		goOrderDetail(item, tabItem) {
			uni.navigateTo({
				url: `/pages/order/orderDetail?order_sn=${item.order_sn}&state=${tabItem.state}`
			})
		},
		goReview(item, tabItem) {
			if (item.products.length > 1) {
				this.goOrderDetail(item, tabItem)
			} else {
				uni.navigateTo({
					url: `/pages/order/review?id=${item.products[0].id}&state=${tabItem.state}`
				})
			}
			
		},
		//swiper 切换
		changeTab(e){
			this.tabCurrentIndex = e.target.current || e.detail.current;
			this.loadData('tabChange');
		},
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
			this.loadData('tabChange')
		},
		//删除订单
		deleteOrder(item, index){
			let form = {
				order_sn: item.order_sn
			}
			uni.showModal({
				content: '确认删除订单吗？',
				success: (e) => {
					if (e.confirm) {
						this.$http.post('order.del', form).then(res => {
							this.setReload(0)
							this.setReload(this.tabCurrentIndex)
							this.loadData()
						})
					}
				}
			})
		},
		//取消订单
		cancelOrder(item){
			let form = {
				order_sn: item.order_sn
			}
			uni.showModal({
				content: '确认取消订单吗？',
				success: (e) => {
					if (e.confirm) {
						this.$http.post('order.cancel', form).then(res => {
							this.setReload(0)
							this.setReload(this.tabCurrentIndex)
							this.loadData()
						})
					}
				}
			})
		},
		goPay(item) {
			uni.navigateTo({
				url: '/pages/money/pay?order_sn=' + item.order_sn
			})
		},
		
		// 订单收货
		receive(item) {
			let form = {
				order_sn: item.order_sn
			}
			uni.showModal({
				content: '确认收货吗？',
				success: (e) => {
					if (e.confirm) {
						this.$http.post('order.receive', form).then(res => {
							this.setReload('all')
							this.loadData()
						})
					}
				}
			})
				
		},
		setReload($index) {
			if ($index == 'all') {
				this.navList.forEach((item, index) => {
					item.reload = true
					item.page = 1
					this.$set(this.navList, index, item)
				})
			} else {
				let item = this.navList[$index]
				item.reload = true
				item.page = 1
				this.$set(this.navList, $index, item)
			}
		},
		goAfterSale(item, tabItem) {
			this.refund_order_sn = item.order_sn
			this.$refs.$afterSaleBox.open()
		},
		goShipStatus(item) {
			this.currentOrder = item;
			setTimeout(() => {
				this.$refs.$expressBox.open();
			}, 1)
		},
		handleOrderRefund() {
			this.setReload('all')
			this.loadData()
		},
		showTip(item) {
			uni.showToast({
				icon: 'none',
				title: item.after_sale_status_tip.tip
			})
		}
	},
}
